@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", "Arial";
}

.load {
  position: relative;
  height: 100vh;
  width: 100vw;
}

.load > img {
  height: 150px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navbar {
  height: 10vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
}

.logo > img {
  width: 2.5rem;
  border-radius: 8px;
}

.nav-list {
  list-style-type: none;
  justify-self: center;
}
.navbar > * {
  margin: 0.8rem;
}

.list-elements a {
  text-decoration: none;
  color: black;
  font-size: 1.1rem;
  margin: 0 0.8rem;
}

.helper {
  justify-self: end;
  display: flex;
}

.helper > * {
  margin: 0 0.8rem;
}

.search-field {
  display: flex;
}

input[type="text"] {
  border: none;
  background: transparent;
  border-bottom: 1px solid black;
  font-size: 1.2rem;
}

input[type="text"]::-webkit-input-placeholder {
  font-size: 1.4rem;
  padding: 0.4rem;
}

input[type="text"]:-ms-input-placeholder {
  font-size: 1.4rem;
  padding: 0.4rem;
}

input[type="text"]::placeholder {
  font-size: 1.4rem;
  padding: 0.4rem;
}

input[type="text"]:focus {
  outline: none;
  border-bottom: 2px solid black;
}

.profile,
.cart-icon,
.search-field {
  font-size: 1.6rem;
  cursor: pointer;
}

.cart-icon {
  position: relative;
}

.cart-value {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 16px;
  margin-top: -0.5rem;
  margin-right: -0.5rem;
  padding: 0.2rem;
  color: white;
  background-color: red;
  border-radius: 10px;
}

.cart-empty {
  display: none;
}

@media only screen and (max-width: 1336px) and (min-width: 1101px) {
  .logo > img {
    width: 30px;
    height: auto;
  }
  .list-elements a {
    font-size: 0.8rem;
  }

  .search-field {
    font-size: 0.8rem;
  }

  input[type="text"] {
    font-size: 0.8rem;
  }

  input[type="text"]::-webkit-input-placeholder {
    font-size: 0.8rem;
  }

  input[type="text"]:-ms-input-placeholder {
    font-size: 0.8rem;
  }

  input[type="text"]::placeholder {
    font-size: 0.8rem;
  }

  .profile,
  .cart-icon {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 1100px) and (min-width: 501px) {
  .navbar {
    display: flex;
    justify-content: space-between;
  }

  .collapse-menu {
    cursor: pointer;
  }

  .nav-list {
    display: flex;
    align-items: center;
    position: absolute;
    background-image: linear-gradient(to right, red, orange);
    margin: 0;
    z-index: 10;
    width: 100%;
    height: 100vh;
    top: 0;
  }

  .list-elements {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  .list-elements > a {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #fff;
  }

  .collapse-menu.open {
    z-index: 10;
    transition: all 500ms;
  }
  .collapse-menu.open .bar:first-child {
    width: 0;
    z-index: 10;
  }
  .collapse-menu.open .bar:last-child {
    width: 0;
    z-index: 10;
  }
  .collapse-menu.open .bar:nth-child(2) {
    background-color: white;
    z-index: 10;
  }

  .list-open {
    margin-top: -110vh;
    transition: all 500ms;
  }

  .list-close {
    margin-top: 0;
    transition: all 500ms;
  }

  .profile,
  .cart-icon {
    font-size: 1.4rem;
    /* z-index: -1; */
  }

  .bar {
    width: 30px;
    height: 5px;
    background-color: #000;
    margin-bottom: 0.2rem;
  }

  .search-field {
    display: none;
    pointer-events: none;
  }
}

@media only screen and (max-width: 500px) {
  .navbar {
    display: flex;
    justify-content: space-between;
  }

  .nav-list {
    display: flex;
    align-items: center;
    position: absolute;
    background-image: linear-gradient(to right, red, orange);
    margin: 0;
    z-index: 10;
    width: 100%;
    height: 100vh;
    top: 0;
  }

  .list-elements {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  .list-elements > a {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #fff;
  }

  .collapse-menu.open {
    z-index: 10;
    transition: all 500ms;
  }
  .collapse-menu.open .bar:first-child {
    width: 0;
    z-index: 10;
  }
  .collapse-menu.open .bar:last-child {
    width: 0;
    z-index: 10;
  }
  .collapse-menu.open .bar:nth-child(2) {
    background-color: white;
    z-index: 10;
  }

  .list-open {
    margin-top: -110vh;
    transition: all 500ms;
  }

  .list-close {
    margin-top: 0;
    transition: all 500ms;
  }

  .collapse-menu {
    cursor: pointer;
  }

  .profile,
  .cart-icon {
    font-size: 1.4rem;
    /* z-index: -1; */
  }

  .bar {
    width: 30px;
    height: 5px;
    background-color: #000;
    margin-bottom: 0.2rem;
  }

  .search-field {
    display: none;
    pointer-events: none;
  }
}

.banner {
  height: 10vh;
  background-image: linear-gradient(to right, #1c1816, #eb4d08);
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner-text {
  text-align: center;
  color: white;
  font-weight: 200;
}

.btn-invite {
  border: none;
  background-color: rgb(255, 255, 255);
  padding: 1rem;
  border-radius: 30px;
  margin-left: 1.4rem;
  cursor: pointer;
}

.profile,
.cart {
  cursor: pointer;
}

@media only screen and (max-width: 1336px) and (min-width: 1101px) {
  .banner-text {
    font-size: 0.8rem;
  }
  .btn-invite {
    padding: 0.4rem;
  }
}

@media only screen and (max-width: 1100px) and (min-width: 501px){
  .banner {
    height: 20vh;
    padding: 1rem 0.8rem;
    font-size: 14px;
    flex-direction: column;
  }

  .banner-text {
    text-align: center;
  }

  .btn-invite {
    text-align: center;
    margin-top: 1rem;
    padding: 0.4rem 0.8rem;
  }
}

@media only screen and (max-width: 500px) {
  .banner {
    height: 20vh;
    padding: 1rem 0.8rem;
    font-size: 14px;
    flex-direction: column;
  }

  .banner-text {
    text-align: center;
  }

  .btn-invite {
    text-align: center;
    margin-top: 1rem;
    padding: 0.4rem 0.8rem;
  }
}

.container {
  font-size: 50px;
}

.Info-bar {
  margin: 0.5rem;
}

.page-nav {
  margin: 1rem 0;
  list-style-type: none;
  display: flex;
}

.page-nav li {
  font-size: 14px;
  font-weight: 500;
  padding-left: 0.2rem;
  color: #8e8e8e;
  cursor: pointer;
}
.page-nav li:last-child {
  color: #000;
}

.page-nav li::before {
  content: "/";
  color: #8e8e8e;
}

.page-nav li:last-child::before {
  color: #000;
}
.page-nav li:first-child::before {
  content: "";
}

.products {
  color: #8e8e8e;
}

.count {
  font-size: 2rem;
}

@media only screen and (max-width: 1336px) and (min-width: 1101px) {
  .count {
    font-size: 1.6rem;
  }
  .page-nav li {
    font-size: 12px;
  }
}

@media only screen and (max-width: 1101px) and (min-width: 501px) {
  .Info-bar {
    padding: 0.4rem;
  }
  .count {
    font-size: 1.2rem;
  }
  .page-nav li {
    font-size: 10px;
  }
}

@media only screen and (max-width: 500px) {
  .Info-bar {
    padding: 0.6rem;
  }
  .count {
    font-size: 1rem;
  }
  .page-nav li {
    font-size: 10px;
  }
}

.product-image {
  max-width: 14rem;
  display: block;
  margin: 0 auto;
  margin-bottom: 0.4rem;
  object-fit: cover;
}

.card {
  padding: 0.4rem;
  position: relative;
}

.vendor {
  font-size: 16px;
  font-weight: 700;
  color: #000;
}
.name {
  font-size: 14px;
  font-weight: 300;
  color: #8e8e8e;
}

.off-price {
  font-size: 16px;
  font-weight: 700;
  color: #000;
}

.original {
  font-size: 14px;
  font-weight: 300;
  color: #8e8e8e;
  text-decoration: line-through;
}

.off-percent {
  font-size: 14px;
  font-weight: 300;
  color: #8e8e8e;
}

.variants {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.7);
  opacity: 0;
}

.card:hover .variants {
  opacity: 1;
}

.variants-list {
  list-style-type: none;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 0 auto;
  margin-top: 2rem;
}

.variant {
  margin-bottom: 0.5rem;
  cursor: pointer;
}

.variant-label {
  font-size: 1.4rem;
  font-weight: 400;
  cursor: pointer;
}

.btn-cart {
  background-image: linear-gradient(to right, red, orange);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 0 auto;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 25px;
  cursor: pointer;
}

.cart {
  text-decoration: none;
  color: white;
}

.product-info {
  width: 90%;
  margin: 0 auto;
}

.toast-shadow {
  box-shadow: none;
}

@media only screen and (max-width: 1336px) and (min-width: 1101px) {
  .variant-label {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 1200px) and (min-width: 501px) {
  .variant-label {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 500px) {
  .product-image {
    display: block;
    max-width: 18rem;
    margin: 0 auto;
    height: auto;
  }
  .product-info {
    margin-top: 0.4rem;
  }
  .card {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    margin-bottom: 1rem;
  }
  .variant-label {
    font-size: 0.8rem;
  }
}

.grid-container {
  padding: 0.5rem;
}

.filter-options {
  list-style-type: none;
  display: flex;
  font-size: 1rem;
}

.filter-bar {
  display: flex;
}

.options {
  margin: 0.8rem 0;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

select {
  width: 15rem;
  margin-right: 1rem;
  padding: 1rem;
  border-radius: 15px;
}
select:focus {
  outline: none;
}
select option {
  padding: 0.5rem;
}

.option {
  padding: 0.5rem 1rem;
  margin: 0 0.5rem;
  border: 1px solid #8e8e8e;
  border-radius: 25px;
  color: #8e8e8e;
  cursor: pointer;
}

.active {
  border: 1px solid #000;
  color: #000;
}
.option:hover {
  color: #000;
  border: 1px solid #000;
}

.filters {
  font-size: 1.6rem;
  font-weight: 400;
}

.grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

@media only screen and (max-width: 1336px) and (min-width: 1101px) {
  .grid-container {
    max-width: 100vw;
  }
  .filter-bar {
    align-items: center;
  }
  .filters {
    font-size: 1.2rem;
  }
  .filter-options {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 1200px) and (min-width: 981px) {
  .grid {
    max-width: 100vw;
    grid-template-columns: repeat(4, 1fr);
  }
}

@media only screen and (max-width: 980px) and (min-width: 751px) {
  .grid {
    max-width: 100vw;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media only screen and (max-width: 750px) and (min-width: 501px) {
  .grid {
    max-width: 100vw;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 800px) {
  .options {
    flex-direction: column;
    max-width: 100vw;
    padding: 0.2rem;
  }
  .filter-bar {
    width: 100vw;
    padding: 0.5rem;
    flex-direction: column;
  }
  .filter-options {
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .option {
    margin: 0.2rem;
  }
  .sort-options {
    margin: 1rem;
  }
  .filters {
    margin-bottom: 0.5rem;
    font-size: 1.2re;
    font-weight: 600;
  }
}

@media only screen and (max-width: 1101px) and (min-width: 501px) {
  .grid-container {
    max-width: 100vw;
  }
  .filters {
    font-size: 12px;
  }
  .filter-bar {
    margin-left: 1rem;
    align-items: center;
  }
  .option {
    font-size: 12px;
  }
}

@media only screen and (max-width: 500px) {
  .grid-container {
    max-width: 100vw;
  }
  .grid {
    max-width: 100vw;
    grid-template-columns: repeat(1, 1fr);
  }
  .options {
    flex-direction: column;
    max-width: 100vw;
    padding: 0.2rem;
  }
  .filter-bar {
    width: 100vw;
    padding: 0.5rem;
    flex-direction: column;
  }
  .filter-options {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .option {
    margin: 0.2rem;
  }
  .sort-options {
    margin: 1rem;
  }
  .filters {
    margin-left: 1rem;
    margin-bottom: 0.5rem;
    font-size: 14px;
  }
}

.close {
  margin-right: -110vw;
  transition: all 500ms;
}

.open {
  margin-right: 0;
  transition: all 500ms;
}

.cart-bar {
  position: fixed;
  overflow-y: scroll;
  z-index: 10;
  background-color: #ccc;
  width: 30vw;
  height: 100vh;
  right: 0;
  top: 0;
  box-shadow: -2px 0 10px #333;
}

.close-cart {
  text-align: end;
  padding-right: 1rem;
  margin-top: 1rem;
  font-size: xx-large;
  font-weight: 500;
  cursor: pointer;
}
.cart-head {
  width: 100%;
  text-align: center;
  padding: 1rem;
  font-size: 1.6rem;
  letter-spacing: 0.1rem;
}

.cart-bar-item {
  display: flex;
  max-width: 90%;
  margin: 0 auto;
  margin-bottom: 0.8rem;
  padding: 0.8rem;
  border-radius: 12px;
  background-color: #fff;
}

.cart-bar-info {
  margin-left: 0.8rem;
}

.cart-bar-info > * {
  margin-bottom: 0.4rem;
}
.cart-bar-name {
  font-size: 1.2rem;
  font-weight: 700;
}
.cart-bar-variant {
  font-size: 1.2rem;
  font-weight: 500;
}
.cart-bar-price {
  font-size: 1.2rem;
  font-weight: 400;
  margin-bottom: 0.2rem;
}

.cart-bar-img {
  height: 100px;
  width: auto;
  object-fit: contain;
}

.quantity-btn {
  display: flex;
  padding-top: 0.5rem;
  border-top: 1px solid #ccc;
}

.quantity,
.quantity-tag {
  padding: 0.4rem 0.8rem;
  font-size: 16px;
}

.quantity-tag {
  font-size: 18px;
}

.quantity {
  background-color: orange;
}
.value {
  background-color: #fff;
}

.reduce {
  border-bottom-left-radius: 12px;
  font-weight: 700;
  border-top-left-radius: 12px;
  cursor: pointer;
}
.increase {
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
  font-weight: 700;
  cursor: pointer;
}

.cta-btns {
  display: flex;
  margin-bottom: 2rem;
  max-width: -webkit-max-content;
  max-width: -moz-max-content;
  max-width: max-content;
  margin: 0 auto;
}

.clear-cart,
.proceed-btn {
  padding: 1rem;
  margin: 0.4rem;
  color: white;
  font-weight: 600;
  letter-spacing: 0.1rem;
  border-radius: 12px;
  cursor: pointer;
}

.clear-cart {
  background-color: orangered;
}

.proceed-btn {
  background-color: #1ddb2c;
}

.empty-img {
  display: block;
  width: 250px;
  margin: 0 auto;
  margin-top: 4rem;
}

.empty-info {
  text-align: center;
  margin-top: 2rem;
  font-size: 1.8rem;
}

